
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  text-decoration: none;
  list-style: none;
  scroll-behavior: smooth;
  scroll-padding-top: 2rem;
}

:root {
  --bg-color: #10101a;
  --main-color: #2fbf71;
  --2nd-main-color: #ffffffcc;
  --text-color: #fff;
  --big-font: 5rem;
  --h2-font: 1.9rem;
}

body {
  background: #10101a;
  background: var(--bg-color);
  color: #fff;
  color: var(--text-color);
}

header {
  position: fixed;
  width: 100%;
  z-index: 1000;
}

section {
  padding: 80px 14%;
}

@media (max-width: 1430px) {
  section {
    padding: 70px 3%;
  }

  :root {
    --big-font: 4rem;
    --h2-font: 1.2rem;
  }
}

@media (max-width: 1020px) {
  :root {
    --big-font: 3rem;
    --h2-font: 1.2rem;
    --p-font: 0.9rem;
  }
}

.menu {
  background: #0b0b13;
  height: 75px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.6);
}

.menu__logo {
  color: #fff;
  font-size: 35px;
  padding: 0 80px;
  font-weight: bold;
}

.menu__logo a {
  text-decoration: none;
  color: #fff;
}

.menu__items {
  display: flex;
  list-style: none;
  margin-right: 30px;
}

.menu__items li {
  border-radius: 3px;
  margin: 0 5px;
}

.menu__items li a {
  padding: 7px 13px;
  text-decoration: none;
  font-size: 18px;
  display: block;
  text-transform: uppercase;
  color: #fff;
}

.menu__items li:hover {
  background-color: var(--main-color);
  color: #fff;
  transition: 0.4s;
}

.menu__btn-menu {
  margin-right: 30px;
  font-size: 25px;
  color: #fff;
  cursor: pointer;
  display: none;
}

.menu ul.show {
  top: 65px !important;
}

@media screen and (max-width: 952px) {
  .menu__logo {
    font-size: 30px;
    padding-left: 35px;
  }
  .menu__items li a {
    font-size: 16px;
  }
}

@media screen and (max-width: 858px) {
  .menu {
    height: 65px;
  }
  .menu__btn-menu {
    display: inline-flex;
  }
  .menu__items {
    position: fixed;
    width: 100vw;
    height: calc(100% - 65px);
    background: #414141;
    top: -100vh;
    text-align: center;
    transition: all 0.4s;
    flex-direction: column;
  }
  .menu__items li {
    margin: 30px 0 0 0;
    line-height: 50px;
  }
  .menu__items li a {
    font-size: 20px;
    color: #fff;
    font-weight: bold;
  }
  .menu__items li:hover {
    background: var(--main-color);
  }
  .menu__logo {
    font-size: 25px;
    padding-left: 35px;
  }
}

@media screen and (max-width: 858px) and (orientation: landscape) {
  .menu__items {
    overflow: scroll;
    height: calc(100% - 65px);
  }
  .menu__items li {
    margin: 5px 0 0 0;
  }
}
/*# sourceMappingURL=Navbar.css.map */
.home {
  position: relative;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 6rem;
  align-items: center;
}

.home__img img {
  max-width: 100%;
  width: 600px;
  height: auto;
}

.home__text h4 {
  color: var(--text-color);
  font-size: 25px;
  font-weight: 500;
  margin: 0 0 20px;
}

.home__text h2 {
  font-size: var(--big-font);
  color: var(--text-color);
  font-weight: 700;
  line-height: 1;
  margin: 0 0 20px;
}

.home__text h6 {
  color: var(--text-color);
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 25px;
}

.home__text h6 span {
  color: var(--main-color);
}

.home__text p {
  max-width: 470px;
  font-size: var(--p-font);
  font-weight: 300;
  line-height: 2rem;
  margin-bottom: 1.5rem;
  color: var(2nd-main-color);
}

.home__text__btn {
  background: var(--main-color);
  color: var(--text-color);
  box-shadow: 4px 4px var(--text-color);
  padding: 0 25px;
  line-height: 42px;
  display: inline-block;
  border: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
  transition: ease all 0.35s;
}

.home__text__btn:hover {
  background: var(--text-color);
  color: var(--main-color);
  box-shadow: 4px 4px var(--main-color);
}

@media (max-width: 1020px) {
  .home {
    height: 70vh;
  }
}

@media (max-width: 795px) {
  .home {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    text-align: center;
    height: 100vh;
  }
  .home__text p {
    max-width: 100%;
  }
  .home__img img {
    max-width: 100%;
    width: 320px;
    height: auto;
  }
}
/*# sourceMappingURL=Home.css.map */
.about {
  display: grid;
  grid-template-columns: repeat(2, 2fr);
  grid-gap: 5rem;
  align-items: center;
}

.about__img img {
  max-width: 100%;
  width: 580px;
}

.about__text h3 {
  color: var(--text-color);
  font-size: 30px;
  font-weight: 300;
  letter-spacing: 6px;
  margin: 0 0 30px;
}

.about__text p {
  max-width: 570px;
  font-size: var(--p-font);
  font-weight: 300;
  margin-bottom: 1.5rem;
  line-height: 1.8rem;
  color: var(2nd-main-color);
}

.about__text__count {
  margin-bottom: 2rem;
}

.about__text__count p {
  color: var(2nd-main-color);
  margin-right: 20px;
  word-spacing: 5px;
}

.about__text__count p span {
  color: var(--text-color);
  font-size: 2.7rem;
  font-weight: 600;
}

.about__text__main-btn a {
  background: var(--main-color);
  color: var(--text-color);
  box-shadow: 4px 4px var(--text-color);
  padding: 0 25px;
  line-height: 42px;
  display: inline-block;
  border: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
  transition: ease all 0.35s;
  margin-right: 20px;
}

.about__text__main-btn a:hover {
  background: var(--text-color);
  color: var(--main-color);
  box-shadow: 4px 4px var(--main-color);
}

@media (max-width: 795px) {
  .about {
    margin-top: 100px;
    grid-template-columns: 1fr;
    text-align: center;
  }
  .about__text h3 {
    max-width: 100%;
  }
  .about__text p {
    max-width: 100%;
  }
  .about__img {
    order: 2;
  }
}
