.services__center {
  text-align: center;
}

.services__center h3 {
  color: var(--text-color);
  font-size: 30px;
  font-weight: 300;
  letter-spacing: 6px;
}

.services__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  grid-gap: 2rem;
  align-items: center;
  margin-top: 4.4rem;
}

.services__content__row {
  background: #0b0b13;
  padding: 60px 35px;
  border-radius: 5px;
  transition: all 0.40s;
  cursor: crosshair;
}

.services__content__row i {
  color: var(--main-color);
  font-size: 3rem;
  margin-bottom: 20px;
}

.services__content__row h4 {
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 20px;
  color: var(--text-color);
}

.services__content__row p {
  font-size: 18px;
  color: var(2nd-main-color);
  line-height: 30px;
  text-decoration: none;
}

.services__content__row__links-div {
  display: flex;
  justify-content: space-around;
  margin-top: 1.5rem;
}

.services__content__row__links-div a {
  color: var(--main-color);
  font-weight: 800;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.services__content__row:hover {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
  box-shadow: 0 15px 30px 0;
}
