  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.resume__content__box:hover {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
  box-shadow: 0 15px 30px 0;
}
